import React, { Component } from 'react';
import bullet from './../images/check.png';
class Faq extends Component{


	render(){

		return(
			<section  id="faqSection" className="faq">
				<div className="faq__background">
					<div className="block__container">
						<h2 className="faq__title">Preguntas frecuentes</h2>
						<ul className="faq__list">
							<li className="faq__list--item">
								<div className="faq__list--question">
									<h3 className="faq__list--question-title">De dnde sacis los datos?</h3>
								</div>
								<div className="faq__list--answer">
									<p className="faq__list--answer-text">Todos los datos son de elaboracin propia de UDA aunque nos nutrimos de informacin de: INE, Catastro, Colegio de Registradores, Consejo General del Notariado, agregadores de Portales Inmobiliarios, APIs inmobiliarios, OSM, Portales de OpenData, Redes sociales, etc.</p>
								</div>
							</li>
							<li className="faq__list--item">
								<div className="faq__list--question">
									<h3 className="faq__list--question-title">Qu pasa si me paso de la cuota?, me vais a cobrar ms automticamente?
									</h3>
								</div>
								<div className="faq__list--answer">
									<p className="faq__list--answer-text">El sistema te informar de que no tienes ms cuotas. Lo normal es que contactes con nosotros y te informamos de las condiciones para ampliar el lmite de peticiones.
									</p>
								</div>
							</li>
							<li className="faq__list--item">
								<div className="faq__list--question">
									<h3 className="faq__list--question-title">Cmo se define una peticin? cada vez que se carga la pgina mide una peticin?</h3>
								</div>
								<div className="faq__list--answer">
									<p className="faq__list--answer-text">Cada vez que t sistema invoca a nuestra API contabilizamos una peticin. Si habis cacheado la informacin no siempre que la pgina se cague vamos a contabilizar la peticin.</p>
								</div>
							</li>
							<li className="faq__list--item">
								<div className="faq__list--question">
									<h3 className="faq__list--question-title">Qu informacin mnima necesita el API para devolver valores?
									</h3>
								</div>
								<div className="faq__list--answer">
									<p className="faq__list--answer-text"> Con una ubicacin es suficiente. Si nos das ms informacin podemos precisar ms. Por ejemplo, si incluyes la superficie de las viviendas por las que preguntas te damos ms grado de detalle.</p>
								</div>
							</li>
							<li className="faq__list--item last-question">
								<div className="faq__list--question">
									<h3 className="faq__list--question-title">Qu tipo de preguntas le puedo hacer a vuestra API?
									</h3>
								</div>
								<div className="faq__list--answer">
									<ul className="faq__list--answer-text">
										<li className="faq__list--answer-text-li"><img src={bullet} alt="-" width="12px" /> Cunto vale una casa? Por cunto alquilo una casa?</li>
										<li className="faq__list--answer-text-li"><img src={bullet} alt="-" width="12px" /> Cul es el precio medio de un barrio?</li>
										<li className="faq__list--answer-text-li"><img src={bullet} alt="-" width="12px" /> Cul es el histrico de precios de una ciudad?</li>
										<li className="faq__list--answer-text-li"><img src={bullet} alt="-" width="12px" /> Cul es la tendencia de precios de una casa como la ma?</li>
										<li className="faq__list--answer-text-li"><img src={bullet} alt="-" width="12px" /> Cul es la rentabilidad de alquiler de mi ciudad?</li>
									</ul>
								</div>
							</li>
						</ul>
					</div>
				</div>
			</section>
		);
	}
}

export default Faq;



